import React, { useEffect } from "react";
import { Container, Typography, Button, Box } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import '../assets/styles/BlogPost.scss';
import { BlogPostProps } from '../data/blogPosts';

interface BlogPostComponentProps {
  post: BlogPostProps;
  onBack: () => void;
}

function BlogPost({ post, onBack }: BlogPostComponentProps) {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <Container className="blog-post-container" style={{paddingTop: 128}}>
      
      <article className="blog-post-content">
        <header className="blog-post-header">
          <Typography variant="h3" component="h1" className="blog-post-title">
            {post.title}
          </Typography>
          <Box className="blog-post-meta">
            <Typography variant="body2" className="blog-post-date">
              {post.date}
            </Typography>
            <Typography variant="body2" className="blog-post-read-time">
              {post.readTime}
            </Typography>
          </Box>
        </header>
        
        <div className="blog-post-body">
          {post.content}
        </div>
      </article>
    </Container>
  );
}

export default BlogPost; 