import React, {useState, useEffect} from "react";
import { useSearchParams } from "react-router-dom";
import {
  Navigation,
  HomePage,
  BlogPost,
} from "./components";
import { BlogPostProps, blogPosts } from './data/blogPosts';
import './index.scss';

function App() {
    const [mode, setMode] = useState<string>('dark');
    const [searchParams, setSearchParams] = useSearchParams();
    const [selectedBlogPost, setSelectedBlogPost] = useState<BlogPostProps | null>(null);

    const handleModeChange = () => {
        if (mode === 'dark') {
            setMode('light');
        } else {
            setMode('dark');
        }
    }

    // Initialize blog post from URL parameter
    useEffect(() => {
        const blogId = searchParams.get('blog');
        if (blogId) {
            const post = blogPosts.find(p => p.id === blogId);
            if (post) {
                setSelectedBlogPost(post);
            } else {
                // If blog post not found, clear the parameter
                setSearchParams({});
            }
        }
    }, [searchParams, setSearchParams]);

    const handleBlogPostSelect = (post: BlogPostProps) => {
        setSelectedBlogPost(post);
        setSearchParams({ blog: post.id });
        window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
    };

    const handleBackToHome = () => {
        setSelectedBlogPost(null);
        // Clear the blog parameter from URL
        setSearchParams({});
        // Scroll to blog section after returning to home
        setTimeout(() => {
            const blogElement = document.getElementById('blog');
            if (blogElement) {
                blogElement.scrollIntoView({ behavior: 'smooth' });
            }
        }, 100);
    };

    useEffect(() => {
        window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
      }, []);

    return (
    <div className={`main-container ${mode === 'dark' ? 'dark-mode' : 'light-mode'}`}>
        <Navigation 
            parentToChild={{mode}} 
            modeChange={handleModeChange}
            isViewingBlogPost={!!selectedBlogPost}
            onBackToHome={handleBackToHome}
        />
        {selectedBlogPost ? (
            <BlogPost 
                post={selectedBlogPost} 
                onBack={handleBackToHome}
            />
        ) : (
            <HomePage onBlogPostSelect={handleBlogPostSelect} />
        )}
    </div>
    );
}

export default App;