import React from 'react';

interface BlogHeadingProps {
  level: 1 | 2 | 3 | 4 | 5 | 6;
  children: React.ReactNode;
}

export const BlogHeading = ({ level, children }: BlogHeadingProps) => {
  const Tag = `h${level}` as keyof JSX.IntrinsicElements;
  return <Tag className={`blog-heading-${level}`}>{children}</Tag>;
};

interface BlogParagraphProps {
  children: React.ReactNode;
}

export const BlogParagraph = ({ children }: BlogParagraphProps) => {
  return <p className="blog-paragraph">{children}</p>;
};

interface BlogImageProps {
  src: string;
  alt: string;
  caption?: string;
  size?: 'small' | 'large';
  height?: string;
}

export const BlogImage = ({ 
  src, 
  alt, 
  caption, 
  size = 'large', 
  height 
}: BlogImageProps) => {
  const dimensions = {
    small: { maxHeight: '30vh' },
    large: { maxHeight: '60vh' }
  };

  const imageHeight = height || dimensions[size].maxHeight;

  return (
    <div className="blog-image-container" style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      margin: '2rem 0',
      width: '100%'
    }}>
      <img 
        src={src} 
        alt={alt} 
        style={{ 
          width: '100%',
          maxHeight: imageHeight,
          objectFit: 'contain',
          borderRadius: '8px',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)'
        }} 
      />
      {caption && (
        <figcaption style={{
          fontStyle: 'italic',
          color: '#666',
          fontSize: '0.9rem',
          textAlign: 'center'
        }}>
          {caption}
        </figcaption>
      )}
    </div>
  );
};

interface BlogTitleProps {
  children: React.ReactNode;
}

export const BlogTitle = ({ children }: BlogTitleProps) => {
  return <h1 className="blog-post-title">{children}</h1>;
};

interface BlogPostContentProps {
  children: React.ReactNode;
}

const BlogPostContent = ({ children }: BlogPostContentProps) => {
  return <div className="blog-post-content">{children}</div>;
}; 

export default BlogPostContent