import BlogPostContent, { BlogTitle, BlogHeading, BlogParagraph, BlogImage } from './BlogPostComponents';
import whisperarch from "../assets/images/whisper.png"
import classifiermodel from "../assets/images/classifier_model.png"
import tsne from "../assets/images/visualisations-tsne.png"
import wav2vec2 from "../assets/images/wav2vec2.png"
import phonemes from "../assets/images/phonemes.png"

export default function AILanguage() {
    return (
        <BlogPostContent>
            {/* <BlogTitle>Building Language Learning Apps with AI</BlogTitle>
            <BlogParagraph>
                The journey began with understanding speech recognition patterns...
            </BlogParagraph> */}
            <BlogHeading level={2}>Whisper, fine-tuning for Scottish</BlogHeading>
            <BlogParagraph>
            During my time at the <a href="https://arxiv.org/html/2505.24832v1" target="_blank" rel="noopener noreferrer" style={{color: 'blue', textDecoration: 'underline'}}>Machine Learning Institute</a>, We got introduced to Whisper, a speech transcription model. To put it simply, it takes audio, converts it into a mel-spectogram with a fourier transform and then uses a CNN to process the mel-spectogram. The result of this is fed into a transformer based encoder. This whole process transforms the audio into a latent space, vectors that represent the audio. The decoder then takes this representation and auto-regressively decodes it into text. After being introduced to Whisper, you quickly realise that it has better performance on well-represented languages/dialects.
            <BlogImage 
                src={whisperarch} 
                alt="Whisper" 
                // caption="Whisper architecture"
            />
            
            As a first task, I wanted to fine-tune it to improve it’s performance on Scottish accents. I first tried doing this by re-training the entire network on a scottish accent dataset. While this improved the models performance on this specific dataset, the fine-tuned model’s performance on data outside of my training set capitulated. This is likely because the model begins overfitting to the specific dataset. 
            This happens as the model is huge while my dataset is small and the model can achieve a low loss just by memorising the entire scottish dataset. It means that its representations are not useful and are unlikely to extend to samples outside of my dataset. This is something to be aware of with neural networks, they can store a certain amount of information per neuron. 
             <a href="https://arxiv.org/html/2505.24832v1" target="_blank" rel="noopener noreferrer" style={{color: 'blue', textDecoration: 'underline'}}> According to this research</a>, neural networks begin by memorising the data and only start generalising when memory has reached capacity as this is the only way to reduce the loss when no more memorisation is possible.
            </BlogParagraph>

            <BlogParagraph>
            One of the solutions to this problem is by using parameter-efficient fine-tuning such as LoRa. This method adds adapters to parts of the neural network, and it is these adapters that are tuned. This has two benefits, first, it keeps most of the model intact so your model does not forget everything it already knows. Secondly, you need far fewer computational resources to fine-tune as you’re only performing back-propagation on parts of the model. You need to compute the gradients of all the neurons that are not frozen and therefore this is a lot cheaper if you’re only changing a subset of the total neurons. 

            After performing fine-tuning with LoRa, I reduced the WER on my Scottish test-set from 11% to 4% which I’d say is very significant.
            </BlogParagraph>

            <BlogHeading level={2}>Building an accent classifier</BlogHeading>
            <BlogParagraph>
                After being introduced to Whisper, I really wanted to use it to build a game that allows you to practice accents. I knew the encoder was already a very good representation for audio so maybe I could use that as a base for an accent classifier.
                My current model takes the audio, converts it to a mel-spectogram and then uses a CNN before putting it through an attention block. The output of the attention block is a matrix which has a dimension of 1500 by 80. The 1500 represents time with each 20ms being represented by 1 of these 1500 rows. My model does a simple average of these 1500 rows and then projects the pooled outputs to the number of accents.
            </BlogParagraph>
            <BlogImage
            src={classifiermodel}
            alt="classifier"
            size="small"
             />
            <BlogParagraph>
            <BlogHeading level={2}>Interpretability</BlogHeading>
            It’s remarkably simple and I’m quite amazed that it works as well as it does. I thought I would have had to experiment with other pooling strategies such as max-pooling or attention pooling but with a validation accuracy of 95%, I did not think I could gain much more by tinkering with the pooling strategy. My current theory for how this model works is down to the different phonemes that people use. People from different places shape their vowels differently and use different phonemic dictionaries. Think of the arab ‘kh’ e.g in the word khalas or the mediterranean rolling R’s or how the spanish have that sound which is in between a Y and a J. I can imagine the model using the dimension of 80 to keep track of the usage of certain sounds. There are multiple ways you could test this. You could do an experiment where you intentionally add phonemes that are associated to a certain place. You could also train the model as a sparse autoencoder with the hope that certain neurons would be representing the use of certain phonemes. If this was the case, they should be activated by a single utterance of that phoneme. Once you’ve been able to distinguish the phonemes in this way, you can then pass in different accents to see if they’re correlating with specific neurons

            <br/><br/>
            I’ve already done an analysis using t-sne to look at how the embeddings of the different accents are represented in 2d and 3d space. 
            </BlogParagraph>
            <BlogImage
                src={tsne}
                size="large"
                alt="tsne"
             />

             <BlogParagraph>
             The thing to remember about t-sne is that it preserves local structure but may lose information about the global structure i.e the distances between clusters may not represent anything. However, if two accents overlap, it means that our model thinks they are similar. We can do a vibe check on overlapping accents, if they are actually quite similar to our ears, then we can be confident that our model really is picking up accents in a similar way to our own ears. I want to repeat the same exercise with UMAP which is much better at preserving global structure. I’m super curious about whether there would be an interesting mapping of accents, maybe it would look a bit like our world map!
            <br />
            <br />
            </BlogParagraph>
            <BlogHeading level={2}>Improving the model</BlogHeading>
            <BlogParagraph>
By far, the best way to improve this model is by collecting more data. Some accents are well represented (mostly anglophone countries) but others have very few samples. Going further, I wonder if there are ways to build this classifier using self-supervised or semi-supervised methods. An idea I had was around using the geographic center of an accent as the target instead of a simple N-class classifier. In theory, the model could then predict accents that were not included in its training set. This would rely on the assumption that accents are related geographically. Cleary, there is some geographic component to accents but I’m unsure if a simple latitude/longitude number would be able to capture the relationship these accents share. To give examples, I do think yorkshire accents are an average of a southern england and scottish accent. However, this same averaging logic probably does not extend beyond the English channel.
<br/><br/>
Another thing I’d like to mention is adding new accents to my model. I started this classifier with just Scottish and English. I then managed to get data for 9 more accents. I thought it would be neat to find a way to add these accents without having to start training from scratch. Unfortunately, this was always going to be difficult with my current architecture as my classifier head only has as many dimensions as the number of accent classes. So I thought it could be interesting to keep the hidden layers frozen and just modify the classifier head so that it has more dimensions to represent the additional accents. Then I would essentially be fine-tuning my model which would use far less computational resources. I trained a model this way and also trained one from scratch and the one trained from scratch actually performed much better after 1 epoch in terms of validation accuracy. An experiment I didn’t get around to was keeping the old weights to initialise the new model but unfreezing them so they can change. This may save some computational resources whenever I want to add new accents.

             </BlogParagraph>

             <BlogHeading level={2}>A model that gives fine-grained feedback</BlogHeading>
             <BlogParagraph>
             When I built the accent game, one of the main bits of feedback I got was that this was a really fun game but that it did not give enough feedback for someone to actually improve their accent/pronunciation. So I wondered if there was a way to give people feedback so they know exactly where they’re making a mistake. It’s such a common problem for a language learner to know the language well but stumble on pronunciation. It’s especially problematic for languages like Chinese and Farsi. To do this, I’d need a model that can recognise phonemes. Amazingly, there is an open source model developed by Meta which has been trained on millions of hours of audio using a BERT-like masked prediction loss. This pre-training step is followed by a fine-tuning step that uses CTC. I used an existing fine-tuned model from huggingface to test out whether this works. 

The thing with this model is that it outputs a predicted phoneme per timestep. This per-timestep output is a vector of size v (total vocabulary) with each element representing the probability that the specific token was said in that timestep. 

             </BlogParagraph>
             <BlogImage
                src={wav2vec2}
                size="large"
                alt="wav2vec2"
                caption='Wav2Vec2 training architecture'
              />
              <BlogParagraph>
              Now, this output could vary depending on how much stress you put on specific phonemes. To make it simple, we can use a technique called forced alignment that is an algorithm which finds the highest-probability path for a given sentence. This can then give you 1 probability per phoneme. I used montreal forced alignment to do this and make a simple mock-up that you can experiment with at accentgame.xyz/phoneme
              </BlogParagraph>
              <BlogImage
                src={phonemes}
                size="large"
                alt="phonemes"
               />
               <BlogParagraph>
               Here, you can see an example of how it works. I’m using the e-speak phonemizer to convert sentences into phoneme strings. These phoneme strings are then used for forced alignment. I think this MVP shows that this is possible but there are some issues with this implementation. I found that certain phonemes are always given a probability of 0. My main suspicions for this are that the fine-tuning step may have used a different phoneme dictionary so some of these phonemes are unrecognisable by the model. Another problem could be that the fine-tuning step had no instances of these phonemes being used and therefore the model has learned to always output a probability of 0. I think these problems would be solvable if one did the fine-tuning step themself. The finetuned model that I used was fine-tuned on many different languages, I suspect this degrades the performance and you’d actually want language specific models.
               </BlogParagraph>
            
        </BlogPostContent>
    )
}