import React from "react";
import { Card, CardContent, Typography, Box } from '@mui/material';
import '../assets/styles/Blog.scss';
import { blogPosts, BlogPostProps } from '../data/blogPosts';

interface BlogListProps {
  onBlogPostSelect: (post: BlogPostProps) => void;
}

function BlogList({ onBlogPostSelect }: BlogListProps) {
  const handleBlogClick = (post: BlogPostProps) => {
    if (post.url) {
      // Open external Medium link in new tab
      window.open(post.url, '_blank', 'noopener,noreferrer');
    } else {
      // Call the callback to show local blog post
      onBlogPostSelect(post);
    }
  };

  return (
    <div className="blog-container" id="blog">
      <div className="items-container">
        <h1 className="text-3xl font-bold text-center mb-16">Blog</h1>
        <Box className="blog-posts-list">
          {blogPosts.map((post) => (
            <Card 
              key={post.id}
              className="blog-post-card"
              onClick={() => handleBlogClick(post)}
              sx={{ 
                cursor: 'pointer',
                marginBottom: 2,
                backgroundColor: 'transparent',
                backdropFilter: 'blur(10px)',
                borderBottom: '1px solid rgba(128, 128, 128, 0.3)',
                '&:hover': {
                  backgroundColor: 'rgba(128, 128, 128, 0.1)',
                  transform: 'translateY(-2px)',
                  transition: 'all 0.3s ease'
                }
              }}
            >
              <CardContent>
                <Typography variant="h5" component="h2" className="blog-post-title">
                  {post.title}
                </Typography>
                <Box className="blog-post-meta" sx={{ display: 'flex', gap: 1, flexDirection: 'row', }}>
                  <Typography variant="body2" className="blog-post-date mb-0">
                    {post.date}
                  </Typography>
                </Box>
                {/* <Typography variant="body1" className="blog-post-excerpt">
                  {post.excerpt}
                </Typography> */}
              </CardContent>
            </Card>
          ))}
        </Box>
      </div>
    </div>
  );
}

export default BlogList; 